ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: #333;
  }
  
  li {
    float: left;
    display: block;
    color: white;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    font-size:1.5rem;

  }


  
img{
    height:2.5rem;
    float:right;
    margin-top:auto;
    margin-top:.5%;
    margin-right:1%;
}