


body {
  background-image: url("./images/starrySky.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size:cover;
  margin:0;
  padding:0;
  height: 100vh;
}

#spacer{
  height:30vh;
}

#resume-img{
  position:absolute;
  width: 97%;
  height:98%;
  top:50%;
  left:50%;
  transform: translate(-50%,-50%);
  border-radius: 2%;
}

.colorful-border{
  position:absolute;
  width:35%;
  height:85%;
  top:52%;
  left:50%;
  transform: translate(-50%,-50%);
  background: linear-gradient(0.25turn, #3f87a6, #ebf8e1, #f69d3c);
  padding:2px;
  border-radius: 2%;
}

@media  screen and (max-width: 1300px) {
  .colorful-border{
    position:absolute;
    width:60%;
    height:85%;
    top:52%;
    left:50%;
    transform: translate(-50%,-50%);
    background: linear-gradient(0.25turn, #3f87a6, #ebf8e1, #f69d3c);
    padding:2px;
    border-radius: 2%;
  }
}

.title{
  color:rgb(39, 38, 38);
  font-size:4rem;
  font-family: 'Libre Franklin', sans-serif;


}

.centeredBox{
  
  text-align: center;
}

.pictureAndText{
  display: flex;
  flex-direction: row;
  width:60%;
  gap:20vw;

  margin:auto;
}

.heroImage{
  width: 20vw;
  height: 30vw;


}

.description{
  background-color: rgba(51,51,51,.5);
  padding:15px;
  border-radius: 5%;
}
a:link {
  color: green;

}